@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Do+Hyeon&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@700&family=Nanum+Myeongjo&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Serif+KR:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.4)),
        url(/react-portfolio/static/media/header-img.baa681f7.jpg);
    background-size: cover;
    color: white;
    text-align: center;
    width: auto;
    height: 50vh;
}
.header h1 {
    display: inline-block;
    font-size: 2em;
    position: relative;
    top: 15vh;
    font-family: "Nanum Gothic", sans-serif;
    z-index: 1000;
}

span {
    display: inline-block;
}
.tag {
    color: black;
    margin: 5px;
    padding: 7px;
    border-radius: 50px;
    font-family: "Do Hyeon", sans-serif;
}
.date {
    font-size: 1em;
    color: #575757;
}
.project {
    margin: 20px;
    padding: 20px 30px;
}
.title {
    color: black;
    font-size: 1.5em;
    margin-right: 10px;
    margin-bottom: 5px;
}

.contents {
    margin-top: 10px;
    color: black;
}

a {
    text-decoration: none;
}

.footer {
    background-color: #35393b;
    background-size: cover;
    color: white;
    text-align: center;
    width: auto;
    height: 200px;
    color: lightgray;
}
.footer div {
    margin: 10px;
    text-align: center;
}
.footer a {
    color: lightgray;
}
a:hover {
    color: rgb(148, 255, 219);
}

.contact-info {
    font-family: "Nanum Gothic";
    margin: 100px;
    padding-top: 10px;
    padding-right: 20px;
}

.image {
    width: 70vw;
}
.header {
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.4)),
        url(/react-portfolio/static/media/header-img.baa681f7.jpg);
    background-size: cover;
    color: white;
    text-align: center;
    width: auto;
    height: 50vh;
}
.header h1 {
    display: inline-block;
    font-size: 2em;
    position: relative;
    top: 10vh;
    font-family: "Nanum Myeongjo", serif;
    z-index: 1000;
}

.header p {
    color: rgb(152, 187, 216);
    font-size: 1.2em;
    position: relative;
    top: 5vh;
    font-family: "Nanum Gothic", serif;
    z-index: 1000;
}
.tags {
    text-align: right;
}

span {
    margin: 10px 10px 50px 10px;
    font-family: "Nanum Gothic", serif;
    color: grey;
}

.project-link {
    font-size: 1.5em;
    color: black;
    font-weight: bold;
    font-family: "Nanum Myeongjo", serif;
    margin: 20px 15px 20px 0px;
}
.detail-contents {
    margin: auto;
    width: 70vw;
}

.br {
    margin: 40px;
}

h2 {
    margin: 50px 0px 20px 0px;
    font-family: "Nanum Myeongjo", serif;
}
a {
    color: rgb(80, 102, 177);
}
a:hover {
    color: rgb(148, 255, 219);
}

@media screen and (min-width: 320px) and (max-width: 768px) {
    .detail-contents {
        width: 90vw;
    }
    .image {
        width: 90vw;
    }
}

body {
    font-family: "Noto Serif KR", serif;
}
.projects {
    margin: 10vh 0;
}
.container {
    margin: auto;
    width: 70vw;
}
h1 {
    font-family: "Nanum Myeongjo", serif;
}
@media screen and (min-width: 320px) and (max-width: 768px) {
    .container {
        width: 90vw;
    }
}

.intro {
    margin: 10vh 0;
}

